import React, { Component } from 'react';
import {Helmet} from "react-helmet";

class Home extends Component {
  render() {
    return (
      <div>
        Home
        <Helmet>
            <title>Our Island</title>
        </Helmet>
      </div>
    );
  }
}

export default Home;



